@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --color-light: white;
    --color-dark: #212121;
    --color-signal: #987100;

    --color-background: var(--color-light);
    --color-text: var(--color-dark);
    --color-accent: var(--color-signal);

    --size-bezel: .5rem;
    --size-radius: 4px;
}
body {
    background: linear-gradient(0deg,#08877a, #015951, #FFC837, #FFF);
    color: #222222;
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
}
img {
    user-select: none;
}

.wrapper {
    width: 90vw;
    margin: 50px auto;
}
header {
    position: relative;
}
header .logo {
    font-weight: 600;
    font-size: 2.4em;
    color: #015951;
    letter-spacing: .1em;
}
header .logo span {
    letter-spacing: 0;
    text-transform: uppercase;
    font-size: 0.8em;
}
header .logo-image {
    display: inline-block;
    float: left;
    position: relative;
    top: -6px;
    width: 52px;
    height: 52px;
    background: url('../public/img/logo1.png') no-repeat center center;
}
header ul.nav {
    position: relative;
    top: 15px;
    float: right;
    list-style: none;
    transition: transform .5s;
}
header ul.nav.active {
    /*transform: translateX(0%);*/
}
header ul.nav li {
    display: inline;
    margin-left: 25px;
    cursor: pointer;
    color: #000000;
    opacity: .5;
    font-weight: 600;
    transition: opacity .5s, color .5s;
}
@media screen and (max-width: 600px) {
    header div {
        /*width: 95vw;*/
    }
    header ul.nav  {
        position: fixed;
        right: 0;
        top: 0;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        font-size: 2em;
        background-color: #fff;
        /*transform: translateX(100%);*/
        z-index: 10000;
        font-weight: 600;
    }
    header ul.nav li {
        margin-bottom: 10px;
        margin-left: 0;
    }
}
header ul.nav li:hover {
    opacity: 1;
    color: #015951;
}

header .shop-cart-button {
    position: relative;
    top: 10px;
    float: right;
    cursor: pointer;
    transition: color, transform 500ms ease;
    font-size: 1.5em;
}

header .shop-cart-button:hover,
header .shop-cart-button.active {
    color: #dc3d3d;
    transform: scale(1.5);
}
header .menu-button {
    position: relative;
    float: right;
    display: none;
    margin-left: 10px;
    top: 10px;
}
@media screen and (max-width: 600px) {
    header .menu-button {
        display: inline-block;
        font-size: 1.5em;
    }
}

header .close-button {
    display: none;
}
@media screen and (max-width: 600px) {
    header .close-button {
        display: inline-block;
        position: absolute;
        top: 10%;
        right: 10%;
    }
}

header .shop-cart {
    position: absolute;
    top: 50px;
    right: 0;
    width: 450px;
    background: #fafafa;
    z-index: 1000;
    -webkit-box-shadow: 4px 5px 15px -7px #606060;
    box-shadow: 4px 5px 15px -7px #606060;
    padding: 20px 20px 0 20px;
}
header .shop-cart .empty h2 {
    font-size: 20px;
    margin-bottom: 20px;
}
@media screen and (max-width: 600px) {
    header .shop-cart .empty h2 {
        float: right;
    }
    header .shop-cart {
        width: 80vw;
    }
}
header .shop-cart .item {
    width: 100%;
    float: left;
    margin-bottom: 20px;
}
header .shop-cart .item img {
    height: 70px;
    float: left;
    margin-right: 20px;
}
header .shop-cart .item h2 {
    font-size: 20px;
    margin-bottom: 10px;
    max-width: 60vw;
    word-break: break-word;
}
@media screen and (max-width: 400px) {
    header .shop-cart .item h2 {
        font-size: 5vw;
        max-width: 60vw;
    }
    header .shop-cart .item img {
        height: 20vh;
    }
}
header .shop-cart .item p {
    color: #797979;
    font-weight: 600;
    position: relative;
}
.order {
    position: absolute;
    display: inline-block;
    bottom: 5px;
    right: 10px;
    text-decoration: none;
    color: #f00;
    font-family: "Century Gothic";
    transition: all .2s;
    margin: 0;
}
.order:hover {
    bottom: 10px;
}
header .shop-cart .item .delete-icon {
    color: #ca5252;
    float: right;
    position: relative;
    top: -25px;
    cursor: pointer;
    transition: color, transform 500ms ease;
}
header .shop-cart .item .delete-icon:hover {
    color: #d83030;
    transform: scale(1.5);
}
header .shop-cart .sum {
    float: left;
    width: 100%;
    font-weight: 600;
    font-size: 20px;
    margin-bottom: 5px;
}
header .presentation {
    position: relative;
    margin: 20px 0;
    width: 100%;
    height: 500px;
    background-size: cover;
    background-blend-mode: multiply;
    background: #bcbcbc url('./img/banner.jpg') no-repeat center center;
}

@media screen and (max-width: 350px) {
    header .presentation {
        width: 95vw;
    }
}

header .presentation::after {
    content: 'Uyingiz uchun eng zo\'r tovarlar';
    position: absolute;
    top: 100px;
    left: 50px;
    width: 250px;
    font-size: 40px;
    font-weight: 600;
    color: #ffffff;
}
header .presentation::before {
    content: 'eng arzon narxda!';
    position: absolute;
    top: 250px;
    left: 50px;
    width: 300px;
    font-size: 15px;
    font-weight: 300;
    color: #ffffff;
}
.categories {
    padding-right: 21vw;
}
.categories div {
    display: inline-block;
    cursor: pointer;
    background: #f2f2f2;
    border-radius: 50px;
    padding: 10px 20px;
    margin-bottom: 10px;
    margin-right: 15px;
    border: 1px solid transparent;
    transition: all .5s ease;
}
@media screen and (max-width: 500px) {
    .categories {
        margin-bottom: 20px;
    }
    .categories div {
        margin-bottom: 5px;
        margin-right: 5px;
    }
}
.categories div:hover {
    border-color: silver;
    transform: scale(1.1);
}

.full-item {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(31, 26, 26, 0.58);
    z-index: 9999;
    overflow: auto;
}
.full-item > div {
    width: 60vh;
    max-width: 90vw;
    max-height: 100vh;
    position: relative;
    top: 0;
    margin: 1% auto;
    padding: 40px 30px;
    background: #fff;
    -webkit-border-radius: 9px;
    -moz-border-radius: 9px;
    border-radius: 9px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
@media screen and (max-width: 500px) {
    .full-item h2 {
        font-size: 1.2em;
    }
}

main {
    display: flex;
    position: relative;
    width: 100%;
    flex-wrap: wrap;
    justify-content: flex-start;
    gap: 2vw;
}
main .item {
    position: relative;
    top: 10px;
    width: 18%;
    margin-bottom: 20px;
    background: #e3e3e3;
    padding-bottom: 20px;
    overflow: hidden;
    border-radius: 10px 10px 0 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
}
main .item p {
    font-size: 16px;
    max-width: 80%;
}
@media screen and (max-width: 500px) {
    main {
        justify-content: space-between;
    }
    main .item {
        width: 45%;
    }
    main .item h2 {
        font-size: 1.2em;
        word-break: break-word;
    }
    main .item p {
        font-size: .8em;
    }
}

@media screen and (max-width: 350px) {
    main {
        width: 95vw;
        justify-content: center;
        gap: 0;
    }
    main .item {
        width: 100%;
    }
}
main .item img, .full-item img {
    width: 100%;
    border-radius: 10px 10px 0 0;
    cursor: pointer;
    transition: transform .5s ease;
}
.full-item img {
    width: 80%;

}
main .item img:hover, .full-item img:hover {
    transform: scale(1.05);
}
main h2, main p, main b,
.full-item h2, .full-item p, .full-item b {
    display: inline-block;
    margin: 10px 20px 0 20px;
    color: #333;
}
main b {
    position: relative;
    left: -20%;
}
main h2 {
    font-size: 1em;
    text-align: center;
}
main b,
.full-item b {
    color: #5fa36a;
}
main .add-to-cart,
.full-item .add-to-cart {
    position: absolute;
    right: 1vw;
    bottom: 1vw;
    background: #ca5252;
    width: 35px;
    height: 35px;
    text-align: center;
    line-height: 35px;
    color: #fff;
    border-radius: 50%;
    cursor: pointer;
    font-weight: 600;
    transition: transform .5s ease;
}
.full-item .add-to-cart {
    right: 0;
    bottom: 0;
}
main .add-to-cart:hover,
.full-item .add-to-cart:hover {
    transform: scale(1.5) translateY(-5px);
}

main .search-form {
    float: right;
    position: absolute;
    top: -7vh;
    right: 10px;
    z-index: 100;
}

@media screen and (max-width: 500px){
    main .search-form {
        width: 100%;
        right: 0;
    }
    .categories {
        padding-right: 0;
        margin-bottom: 50px;
    }
    .categories div {
        margin-bottom: 20px;
    }
}
main .search-form .autocomplete {
    position: absolute;
    left: 0;
    top: 50px;
    width: 100%;
    background: #fff;
    list-style: none;
    box-shadow: 1px 1px 5px rgba(0,0,0,0.15);
    z-index: 101;
    max-height: 240px;
    height: auto;
    overflow: auto;
}
main .search-form .autocomplete-item {
    padding: 10px;
}
main .search-form .autocomplete-item:hover {
    background: rgba(0,0,0,0.4);
    cursor: pointer;
    transition: cubic-bezier(1, 0, 0, 1) .3s all;
}

main .search-form .input {
    position: relative;
}
main .search-form .input__label {
    position: absolute;
    left: 0;
    top: -10px;
    padding: calc(var(--size-bezel) * 0.75) calc(var(--size-bezel) * .5);
    margin: calc(var(--size-bezel) * 0.75 + 3px) calc(var(--size-bezel) * .5);
    white-space: nowrap;
    transform: translate(0, 0);
    transform-origin: 0 0;
    background: var(--color-background);
    transition: transform 120ms ease-in;
    font-weight: bold;
    line-height: 1.2;
    color: #606060;
    border-radius: 10px;
}
main .search-form .input__field {
    display: block;
    width: 100%;
    border: 3px solid currentColor;
    padding: calc(var(--size-bezel) * 1.5) var(--size-bezel);
    margin-top: -10px;
    color: currentColor;
    /*background: transparent;*/
    border-radius: var(--size-radius);
}
main .search-form .input__field:focus + .input__label,
main .input__field:not(:placeholder-shown) + .input__label {
    transform: translate(0.25rem, -65%) scale(0.8);
    color: var(--color-accent);
}
main .search-form .search-img {
    position: absolute;
    top: calc(50% - 5px);
    right: 5px;
    transform: translateY(-50%);
}

.group {
    position:relative;
}
.group input {
    position: relative;
    left: 5%;
    font-size:18px;
    padding:10px 10px 10px 5px;
    display:block;
    width: 77%;
    border:none;
    border-bottom:1px solid #757575;
}
.group input:focus { outline:none; }
.group .bar 	{ position:relative; display:block; width:300px; }
.group .bar:before,
.group .bar:after 	{
    content:'';
    height:2px;
    width:0;
    bottom:1px;
    position:absolute;
    background:#5264AE;
    transition:0.2s ease all;
    -moz-transition:0.2s ease all;
    -webkit-transition:0.2s ease all;
}
.group .bar:before {
    left:44%;
}
.group .bar:after {
    right:56%;
}

.group input:focus ~ .bar:before,
.group input:focus ~ .bar:after {
    width:39%;
}

footer {
    text-align: center;
    margin-top: 50px;
    color: #ffffff;
}